:root {
    --font: 'Inter', sans-serif !important;
    --color: #000;
    --colorOpacity: #000000e0;
    --colorInvert: #fff;
    --colorAction: #7bb0ff;
    --colorDelete: #e63946;
    --colorWhite: #fff;
    --helpColor: #c4c3c6; 
    --bgBody: #fff;
    --bgSecond: #f9f9f9;
    --bgSecondHelper: rgba(0, 0, 0, 0.06);
    --sliderBg: #e5e5e5;
}

@media (prefers-color-scheme: dark) {
    :root {
        --font: 'Inter', sans-serif !important;
        --color: #fff;
        --colorOpacity: #ffffffe0;
        --colorInvert: #000;
        --colorAction: #7bb0ff;
        --colorDelete: #e63946;
        --colorWhite: #fff;
        --helpColor: #c4c3c6; 
        --bgBody: #101010;
        --bgSecond: #181818;
        --bgSecondHelper: rgba(0, 0, 0, 0.75);
        --sliderBg: #e5e5e5;
    }
}