main {
    display: flex;
    flex-flow: column nowrap;
    gap: 1rem;
}
.block {
    background-color: var(--bgBody) !important;
    border-radius: 10px;
    box-shadow: 0px 0px 85px -80px var(--colorAction);
    padding: 20px 40px;
}
.task {
    background-color: var(--bgSecondHelper) !important;
    border-radius: 10px;
    padding: 20px 40px;
}
.button-delete * {
    color: var(--colorDelete) !important;
}
.MuiDialog-paper {
    background-color: var(--bgBody) !important;
}
.button-PROJ {
    min-width: 140px !important;
    width: 30% !important;
    height: 70px !important;
    border-radius: 10px !important;
}
.button-PROJ button {
    width: 100%;
    height: 100%;
    border-radius: 10px !important;
}
.time-page {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: calc(100vh - 100px);
}
button:disabled {
    color: var(--helpColor) !important;
}
.MuiInputLabel-formControl {
    color: var(--helpColor) !important;
}
.slider {
    width: 100% !important;
}
.MuiInputBase-input {
    color: var(--color) !important;
}
.MuiLinearProgress-root {
    height: 8px !important;
    background-color: var(--sliderBg) !important;
    border-radius: 5px !important;
}
.MuiLinearProgress-barColorPrimary {
    background: linear-gradient(90deg, rgba(18, 231, 48, 1) 0%, rgba(0, 212, 255, 1) 100%);
    border-radius: 10px !important;
}
.button-answers button {
    display: flex;
    justify-content: flex-start !important;
    background-color: var(--bgBody) !important;
    color: var(--color) !important;
    border: none !important;
    border: 2px solid var(--sliderBg) !important;
    border-radius: 10px !important;
    font-size: 16px !important;
    padding: 15px 30px !important;
}
